<template>
    <div class="app">
        <header class="app-header">
            Upcoming New Year's Countdowns <a href="https://www.earthcam.com/newyears/"> link to earthcam </a>
        </header>
        <div v-for="(group, index) in groupedTimezones" :key="group.timeUntilNewYear" class="grouped-timezone-countdown">
            <!-- Display the first group with expansion panels for each timezone -->
            <div v-if="index === 0">
                <div class="top-group">
                    {{ group.countdown.days }} days, {{ group.countdown.hours }} hours, {{ group.countdown.minutes }} minutes, {{ group.countdown.seconds }} seconds
                </div>
                <v-expansion-panels v-model="openPanels">
                    <v-expansion-panel v-for="name in group.names" :key="name">
                        <v-expansion-panel-title>TZ: {{ name.replace("/", ": ").replace("_", " ") }}</v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <div v-for="(value, key) in getCountryCodes(name)" :key="key">
                                <h3> {{ key }}</h3>
                                <div v-for="language in value" :key="language">
                                    {{ language.name[0] }} : {{ language.code }}
                                    <v-btn @click="happyNewYear(language.name[0])">Say Happy New Year!</v-btn>
                                </div>
                            </div>
                            More options and additional information for {{ name.replace("/", ": ").replace("_", " ") }}...
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                </v-expansion-panels>
            </div>

            <!-- Display remaining groups without expansion panels -->
            <div v-else>
                <div class="timezone-names">TZs: {{ group.names.join(', ').replaceAll("/", ": ").replaceAll("_", " ") }}</div>
                <div class="countdown-timer">
                    {{ group.countdown.days }} days,
                    {{ group.countdown.hours }} hours,
                    {{ group.countdown.minutes }} minutes,
                    {{ group.countdown.seconds }} seconds
                </div>
            </div>
        </div>
    </div>
</template>

<script setup>
import moment from "moment-timezone";
import countryLanguage from "@ladjs/country-language";
import { ref, reactive, computed, onMounted, onUnmounted } from "vue";

const timezones = ref(moment.tz.names().filter((name) => !name.includes("Etc/"))); // all timezones
const countdowns = reactive({}); // reactive object to hold countdowns
// eslint-disable-next-line no-unused-vars
const openPanels = ref([]);
const alertsTriggered = reactive({});

const synth = window.speechSynthesis;

function getCountryCodes(timezone) {
    const countries = moment.tz.zone(timezone).countries();

    const languageByCountry = {};

    countries.forEach((code) => {
        countryLanguage.getCountry(code, (err, country) => {
            if (err) {
                console.error(err);
            } else {
                languageByCountry[country.name] = [];
                country.languages.forEach((language) => {
                    languageByCountry[country.name].push({ name: language.name, code: language.iso639_1 });
                });
            }
        });
    });
    return languageByCountry;
}

// Calculate countdown for a specific timezone
function calculateCountdown(timezone) {
    const newYear = moment.tz("2024-01-01 00:00", timezone);
    const now = moment.tz(timezone);
    const duration = moment.duration(newYear.diff(now));

    return {
        days: duration.days(),
        hours: duration.hours(),
        minutes: duration.minutes(),
        seconds: duration.seconds(),
        timeUntilNewYear: newYear.diff(now), // total milliseconds until New Year
    };
}

async function happyNewYear(language) {
    const text = new SpeechSynthesisUtterance(`Happy new year in ${language}`);

    // eslint-disable-next-line prefer-destructuring
    text.voice = synth.getVoices()[0];
    synth.speak(text);
}

// Initialize countdowns for all timezones
for (const zone of timezones.value) {
    countdowns[zone] = calculateCountdown(zone);
}

// Group timezones by the countdown time
const groupedTimezones = computed(() => {
    const groups = {};
    for (const zone of timezones.value) {
        const countdown = countdowns[zone];
        // Create a unique key for the group
        const timeKey = `${countdown.days}:${countdown.hours}:${countdown.minutes}:${countdown.seconds}`;
        // Skip past New Year's events
        // eslint-disable-next-line no-continue
        if (countdown.timeUntilNewYear < 0) continue;
        if (!groups[timeKey]) {
            groups[timeKey] = { names: [], countdown, timeUntilNewYear: countdown.timeUntilNewYear };
        }
        groups[timeKey].names.push(zone);
    }
    // Convert the groups object into an array, sort by time until New Year, and take the first 3
    return Object.values(groups)
        .sort((a, b) => a.timeUntilNewYear - b.timeUntilNewYear)
        .slice(0, 3);
});

// Update countdown every second
let intervalId;
onMounted(() => {
    Notification.requestPermission().then((result) => {
        if (result === "granted") {
            // eslint-disable-next-line no-console
            console.log("Notification permissions granted.");
        }
    });
    intervalId = setInterval(() => {
        for (const zone of timezones.value) {
            const newCountdown = calculateCountdown(zone);
            // Check if it's less than 10 minutes and alert has not been triggered yet
            if (newCountdown.timeUntilNewYear < 600000 && newCountdown.timeUntilNewYear >= 0 && !alertsTriggered[zone]) {
                // Use browser notifications or alert
                // eslint-disable-next-line no-new
                new Notification(`New Year is less than 10 minutes away in ${zone}!`);

                alertsTriggered[zone] = true; // Mark as triggered
            } else if (newCountdown.timeUntilNewYear >= 600000 || newCountdown.timeUntilNewYear < 0) {
                alertsTriggered[zone] = false;
            }
            countdowns[zone] = newCountdown; // update countdown
        }
    }, 1000);
});

// Clear interval when component unmounts
onUnmounted(() => {
    clearInterval(intervalId);
});
</script>

  <style>
/* General Styles */
.app {
  font-family: Arial, sans-serif;
  color: #333;
  max-width: 800px;
  margin: 0 auto;
}

.app-header {
  font-size: 2rem;
  text-align: center;
  margin-bottom: 20px;
}

/* Grouped Timezone Countdown Styles */
.grouped-timezone-countdown {
  border-bottom: 1px solid #ccc;
  padding: 20px;
  transition: background-color 0.3s;
}

.grouped-timezone-countdown:hover {
  background-color: #f9f9f9;
}

/* First Group Emphasis */
.grouped-timezone-countdown:first-child {
  font-size: 1.5rem; /* Larger Font */
  background-color: #e8e8e8; /* Different background */
}

/* Countdown Timer Styles */
.countdown-timer {
  font-size: 3rem;
  font-weight: bold;
  margin: 10px 0;
}

/* Timezone Names List */
.timezone-names {
  font-size: 1.2rem;
  margin-bottom: 10px;
  display: block; /* Makes it easier to style individual names later */
}

/* Expandable Menu Placeholder */
.expandable-menu {
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  padding: 10px;
  margin-top: 10px;
  cursor: pointer;
  user-select: none;
}

.expandable-menu:not(:last-child) {
  border-bottom: none;
}

/* Hidden initially */
.expandable-content {
  display: none;
  padding: 10px;
  border-top: 1px solid #ddd;
}

/* Show content when .expandable-menu is clicked */
.expandable-menu.active + .expandable-content {
  display: block;
}

/* Interactive Items */
.interactive-item {
  cursor: pointer;
  user-select: none;
}

.top-group {
  font-size: 1.5rem; /* Larger font size for the top group */
  margin-bottom: 20px;
}
  </style>
